import {HttpRequest} from '@angular/http/client';

import {TestRequest} from './request';

/**
 * Defines a matcher for requests based on URL, method, or both.
 *
 * @experimental
 */
export interface RequestMatch {
  method?: string;
  url?: string;
}

/**
 * Configures special verification behaviors.
 *
 * @experimental
 */
export interface VerifyOptions {
  /**
   * Sets whether to ignore cancelled requests when verifying.
   */
  ignoreCancelled?: boolean;
}

/**
 * Type signature of a function which takes a {@link HttpRequest} and
 * determines whether it matches some predicate.
 *
 * @experimental
 */
export type RequestMatchFn = (req: HttpRequest<any>) => boolean;
/**
 * Controller to be injected into tests, that allows for mocking and flushing
 * of requests.
 * 
 * \@experimental
 * @abstract
 */
export abstract class HttpTestingController {
/**
 * Search for requests that match the given parameter, without any expectations.
 * @abstract
 * @param {?} match
 * @return {?}
 */
match(match: string|RequestMatch|RequestMatchFn) {}
/**
 * @abstract
 * @param {?} url
 * @return {?}
 */
expectOne(url: string) {}
/**
 * @abstract
 * @param {?} params
 * @return {?}
 */
expectOne(params: RequestMatch) {}
/**
 * @abstract
 * @param {?} matchFn
 * @return {?}
 */
expectOne(matchFn: RequestMatchFn) {}
/**
 * @abstract
 * @param {?} match
 * @return {?}
 */
expectOne(match: string|RequestMatch|RequestMatchFn) {}
/**
 * @abstract
 * @param {?} url
 * @return {?}
 */
expectNone(url: string) {}
/**
 * @abstract
 * @param {?} params
 * @return {?}
 */
expectNone(params: RequestMatch) {}
/**
 * @abstract
 * @param {?} matchFn
 * @return {?}
 */
expectNone(matchFn: RequestMatchFn) {}
/**
 * @abstract
 * @param {?} match
 * @return {?}
 */
expectNone(match: string|RequestMatch|RequestMatchFn) {}
/**
 * @abstract
 * @param {?=} opts
 * @return {?}
 */
verify(opts?: VerifyOptions) {}
}
